.settings__background_pic{
  background-image: url("../images/settings_page_background.png");
  animation: move_left 50s linear infinite;
  background-repeat: repeat-x;
  background-size: cover;
  width: inherit;
  height: inherit;
}

@keyframes move_left{
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -4000px 0;
  }
}

.foreground_pic{
  margin-top: 30px;
  width: 50%;
  height: 150px;
  background-image: url("../images/settings_page_foreground.png");
}

.page__header{
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 400px;
  color: white;
  text-align: center;
  h1{
    margin-bottom: 50px;
  }
  p{
    max-width: 1000px;
  }
}

.settings__main{
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 1fr 3fr;
  width: 100%;
}

.education__delete_button{
  $size: 60px;
  border-radius: 25%;
  min-width: $size;
  min-height: $size;
  background-color: darkslategray;
}

.add_field__button{
  color: gray;
  background-color: inherit;
}

$switcher_height: 40px;
$switcher_width: 100px;
.switch-btn {
  display: inline-block;
  width: $switcher_width; /* ширина переключателя */
  height: $switcher_height; /* высота переключателя */
  border-radius: $switcher_height/2; /* радиус скругления */
  background: #bfbfbf; /* цвет фона */
  z-index: 0;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  position: relative;
  transition-duration: 300ms; /* анимация */
}

.switch-btn::after {
  content: "";
  height: $switcher_height; /* высота кнопки */
  width: $switcher_height; /* ширина кнопки */
  border-radius: 50%; /* радиус кнопки */
  background: #fff; /* цвет кнопки */
  left: 0;
  transition-duration: 300ms; /* анимация */
  box-shadow: 0 0 10px 0 #999999; /* тень */
  position: absolute;
  z-index: 1;
}
.switch-on {
  background: orangered;
  box-shadow: inset 0 0 10px 0 #999999; /* тень */
}
.switch-on::after {
  left: $switcher_width - $switcher_height;
}