$lightgrey: #888da8;
$grey_header_color: #494c62;
$header_height: 50px;
$bg_color: lightblue;
$avatar_size: 100px;
$container_elements_h:70%;

* {
  margin: 0;
  padding: 0;
  border: 0;
}
a {
  text-decoration: none;
}
ul {
  list-style-type: none;
}

body{
  background-color: #edf2f6;
}

input {
  border: none;
  background-color: inherit;
  color:inherit;
}


.default_img{
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: inherit;
}

%sidebar_shared {
  position: fixed;
  display: inline-flex;
  flex-direction: column;
  height: 100%;
  width: $header_height;
  top: $header_height;
  background-color: white;
}

.sidebar_left {
  @extend %sidebar_shared;
  overflow: hidden;
  left: 0;
  transition: width 0.3s;
}

.show_menu__effect{
  width: 168px;
}

$sidebar_left_icon_s: 30px;

.sidebar_left__item{
  display: inline-flex;
  align-items: center;
  width: 100%;
  min-height: 50px;
  i{
    display: block;
    margin-left: 10px;
    min-width: $sidebar_left_icon_s;
    height: $sidebar_left_icon_s;
    background-size: 30px;
  }
  span{
    white-space: nowrap;
    margin-left: 20px;
    font-size: 14px;
  }
}


.show_menu__button{
  background-image: url("../images/menu_icon.svg");
}
.show_news__button{
  background-image: url("../images/news_icon.png");
}
.show_messages__button{
  background-image: url("../images/messages_icon.png");
}

.show_friends__button{
  background-image: url("../images/friends_icon.png");
}

.show_communities__button{
  background-image: url("../images/communities_icon.png");
}

.show_music__button{
  background-image: url("../images/music_icon.png");
}

.show_video__button{
  background-image: url("../images/video_icon.png");
}

.show_weather__button{
  background-image: url("../images/weather_icon.png");
}

.show_settings__button{
  background-image: url("../images/settings_icon.png");
}

.sidebar_right {
  @extend %sidebar_shared;
  right: 0;
}

.header_static{
  display: inline-flex;
  align-items: center;

  position: fixed;
  top: 0;
  width: 100%;
  height: $header_height;
  background-color: $grey_header_color;
}


.search_container{
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  width: 250px;
  height: $container_elements_h;
  margin-left: 10px;
}

.search__button{
  flex:5;
  max-width: 15%;
  height: inherit;
  background-image: url("../images/search_icon.png");
}

.search__input{
  flex: 1;
  max-width: 85%;
  height: inherit;
  padding: 0 10px;
  color: white;
  font-size: 0.7em;
  background-color: darken($lightgrey,35%);
}

.header__logo{
  height: $header_height;
  width: $header_height;
}

.page_name{
  color: white;
  margin-left: 10px;
  max-width: 200px;
  overflow: hidden;
}

.notifications__container{
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  height: $container_elements_h;
  width: 100px;
}

.notifications__item{
  width: 20px;
  height: 20px;
  filter: invert(100%);
}

.friend_requests__img{
  background-image: url("../images/friend_requests.png");
}
.chat_messages__img{
  background-image: url("../images/new_messages.png");
}

.other_notifications__img{
  background-image: url("../images/notifications.png");
}

.my_profile{
  margin: 0 $header_height 0 30px;
  display: grid;
  height: 80%;
  grid-template-columns: 30px 5px 1fr 5px 10px;
  grid-template-areas:
          "av . name . ic"
          "av . status . .";
}

.avatar{
  grid-area: av;
  align-self: center;
  border-radius: 50%;
  height: 30px;
  width: 100%;
}
.my_name{
  color: whitesmoke;
  align-self: end;
  font-size: 12px;
  grid-area: name;
}

.my_status{
  color: cadetblue;
  align-self: start;
  font-size: 10px;
  grid-area: status;
}

.acc_icon{
  grid-area: ic;
  align-self: self-end;
  background-image: url("../images/acc_icon.png");
  width: 100%;
  height: 8px;
  margin-bottom: 2px;
  filter: invert(100%);
}

.main_window{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% - 2*#{$header_height});
  margin: $header_height auto 0 auto;
  height: 100%;
}

.default_block{
  display: inline-flex;
  flex-direction: column;
  background-color: #fff;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.default_block__item{
  h2{
    font-size: 16px;
  }
  margin-bottom: 15px;
}

.default_block__header{
  font-size: 26px;
  font-weight: normal;
  border-bottom: 1px solid grey;
  margin-bottom: 20px;
}

.default_margin-bottom{
  margin-bottom: 30px;
}


.default_field{
  width: 100%;
  position: relative;
  font-size: 16px;
  overflow: hidden;
  &:not(:last-child){
    margin-right: 20px;
  }

  input, select{
    height: 75px;
    padding-top: 15px;
  }
  textarea{
    height: 150px;
    padding-top: 20px;
    resize: none;
  }
}

.default_field__label{
  position: absolute;
  top:0;
  left: 5px;
  font-size: 20px;
  color:gray;
  white-space: nowrap;
}

.default_field__input{
  width: 100%;
  height: 100%;
  font-size: 24px;
  border: 1px solid grey;
  box-sizing: border-box;
  border-radius: 0.2em;
  padding-left: 5px;
  padding-right: 5px;
}

.default_container{
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  &:not(:last-child){
    margin-bottom: 20px;
  }
}
.default_line{
  flex-direction: row;
}

.default_column{
  flex-direction: column;
}

.default_link{
  $size: 75px;
  line-height: $size;
  height: $size;
  font-size: 26px;
  white-space: nowrap;
  color: black;
}

.confirm_button{
  height: 75px;
  width: 100%;
  background-color: coral;
  border-radius: 0.25em;
}
.cancel_button{
  height: 75px;
  width: 100%;
  background-color: gray;
  border-radius: 0.25em;
}

.default_button{
  color: white;
  font-size: 24px;
  &:not(:last-child){
    margin-right:  20px;
  }
}

.default_switcher__label{
  font-size: 30px;
}