.page__container{
  display:inline-flex;
  flex-direction: column;
  width: 95%;
}

.profile__header_pics{
  display: flex;
  justify-content: center;
  position: relative;
}

.profile__bg_pic{
  display: block;
  z-index: 0;
  width: 100%;
  height: 500px;
}

.profile__avatar{
  position: absolute;
  border: 5px solid white;
  width: 150px;
  height: 150px;
  bottom: -30px;
}


.navigator{
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: 100px;
  background-color: #fff;
  &__item{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100px;
    font-size: 20px;
  }
  
}

.profile__descr{
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
}

.profile_name{
  z-index: 1;
  font-size: 20px;
  font-weight: bold;
}

.profile_city{
  font-size: 14px;
}

.profile_info__container{
  display: grid;
  grid-template-columns: 1.5fr 4fr 1.5fr;
  width: 100%;
}
